import Vue from 'vue'
import App from './App'
import axios from 'axios'
import VueHtml5Plus from 'vue-html5plus';
import './components/css/info.css';
import { Radio, Loading, Button, Divider } from 'element-ui';
Vue.use(Button);Vue.use(Radio);Vue.use(Divider);Vue.use(Loading);

// Vue.use(VueHtml5Plus);
axios.defaults.timeout = 30000;
console.log(VueHtml5Plus)
// alert(String(VueHtml5Plus))
Vue.prototype.$VueHtml5Plus = VueHtml5Plus

Vue.prototype.$axios = axios;
Vue.config.productionTip = false

new Vue({
  el: '#app',
  components: { App },
  template: '<App/>'
})



