        // },

        //baocun
        saveImg(){
            var wvs= new plus.webview.all();
            alert(wvs)
                var bitmap = new plus.jsNative.Bitmap("test");
                var canvasData = $('#qrcode').children('canvas');
                // 从本地加载Bitmap图片
                bitmap.loadBase64Data(base64,function(){
                // console.log('加载图片成功');
                bitmap.save( "_doc/"+uid+".jpg",{overwrite:true,1:1},function(i){
                    
                    alert('保存图片成功：'+JSON.stringify(i));
